/* PokerRoom.css */

.poker-container {
  max-width: 800px;
  margin: auto;
  padding: 40px 20px;
  text-align: center;
  font-family: 'Segoe UI', sans-serif;
  background: var(--poker-bg);
  min-height: 100vh;
  position: relative;
}

.qr-code-floating {
  position: absolute;
  top: 20px;
  right: 20px;
}

.poker-header h1 {
  color: var(--accent);
  margin-bottom: 20px;
}

.poker-header h3 {
  color: var(--text-secondary);
  font-weight: 500;
  margin-top: -10px;
  margin-bottom: 20px;
}

.invite-section {
  margin-top: 30px;
}

.share-box {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.share-box input {
  padding: 10px;
  font-size: 14px;
  width: 340px;
  border-radius: 6px;
  border: 1px solid #ccc;
  background-color: #f1f5f9;
}

body.dark .share-box input {
  background-color: #1e293b;
  color: #f8fafc;
}

.name-input {
  margin-top: 20px;
  margin-bottom: 20px;
}

.name-input input {
  padding: 12px;
  font-size: 16px;
  width: 60%;
  border-radius: 8px;
  border: 1px solid #ccc;
}

body.dark .name-input input {
  background-color: #1e293b;
  color: #f8fafc;
}

.card-options {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 20px;
}

.card {
  background-color: var(--card-bg);
  color: var(--text-primary);
  font-size: 24px;
  margin: 10px;
  padding: 16px 24px;
  border-radius: 12px;
  border: 2px solid transparent;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.card:hover {
  background-color: #c7d2fe;
}

.card.selected {
  border-color: var(--accent);
  background-color: var(--accent);
  color: white;
}

.action-buttons {
  margin-bottom: 30px;
}

.action-buttons button {
  margin: 10px;
  padding: 12px 20px;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.submit-btn {
  background-color: var(--btn-bg);
  color: white;
}

.reveal-btn {
  background-color: #4ade80;
  color: white;
}

.reset-btn {
  background-color: #f87171;
  color: white;
}

.vote-list {
  margin-top: 30px;
  text-align: left;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}

.vote-list ul {
  list-style-type: none;
  padding: 0;
}

.vote-list li {
  font-size: 18px;
  margin: 10px 0;
}

.vote-breakdown {
  margin-top: 30px;
  text-align: left;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}

.vote-breakdown ul {
  list-style-type: none;
  padding: 0;
}

.vote-breakdown li {
  font-size: 16px;
  margin: 6px 0;
}

.reaction-feed {
  display: none;
}

.copy-btn {
  padding: 10px 14px;
  background-color: var(--accent);
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.copy-btn:hover {
  background-color: var(--btn-hover);
}

.copy-btn.copied {
  background-color: #43a6e8;
  color: #f0fdf4;
}

body.dark .card {
  background-color: #334155;
  color: #f8fafc;
}

.reaction-fab {
  position: fixed;
  right: 20px;
  bottom: 90px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 10px;
  z-index: 5;
}

.reaction-toggle {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  border: none;
  background: var(--btn-bg);
  color: #fff;
  padding: 12px 16px;
  border-radius: 999px;
  cursor: pointer;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
  transition: transform 0.15s ease, box-shadow 0.15s ease;
}

.reaction-toggle .emoji {
  font-size: 20px;
}

.fab-label {
  font-weight: 700;
  letter-spacing: 0.01em;
}

.fab-badge {
  background: #fbbf24;
  color: #0f172a;
  border-radius: 999px;
  padding: 4px 8px;
  font-weight: 800;
  font-size: 12px;
}

.reaction-toggle:hover {
  transform: translateY(-1px);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.2);
}

.reaction-popover {
  width: 320px;
  padding: 14px;
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.95);
  box-shadow: 0 14px 40px rgba(0, 0, 0, 0.16);
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.reaction-header {
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 8px;
}

.reaction-helper {
  margin: 4px 0 0;
  color: var(--text-secondary);
  font-size: 14px;
}

.popover-close {
  border: none;
  background: transparent;
  font-size: 16px;
  cursor: pointer;
  color: var(--text-secondary);
}

.reaction-buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
  flex-wrap: wrap;
}

.reaction-btn {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  border: 1px solid #cbd5e1;
  background: #f8fafc;
  color: #0f172a;
  padding: 10px 12px;
  border-radius: 999px;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
  min-width: 82px;
  justify-content: center;
}

.reaction-btn .emoji {
  font-size: 18px;
}

.reaction-btn .reaction-count {
  font-weight: 700;
  color: #0f172a;
}

.reaction-btn:hover {
  border-color: var(--accent);
  background: #e0f2fe;
}

.reaction-btn.active {
  border-color: var(--accent);
  background: var(--accent);
  color: #fff;
}

.reaction-btn.active .reaction-count {
  color: #fff;
}

body.dark .reaction-toggle {
  background: #1e293b;
  color: #e2e8f0;
}

body.dark .reaction-popover {
  background: #0b1324;
  border: 1px solid #1e293b;
  box-shadow: 0 14px 40px rgba(0, 0, 0, 0.35);
}

body.dark .popover-close {
  color: #cbd5e1;
}

body.dark .reaction-btn {
  background: #1e293b;
  color: #e2e8f0;
  border-color: #334155;
}

body.dark .reaction-btn:hover {
  background: #0ea5e9;
  border-color: #7dd3fc;
}

.floating-reactions {
  position: absolute;
  inset: 0;
  pointer-events: none;
  overflow: hidden;
}

.floating-reaction {
  position: absolute;
  bottom: 32px;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 8px 12px;
  border-radius: 999px;
  background: rgba(37, 99, 235, 0.2);
  color: var(--text-primary);
  font-weight: 700;
  animation: floatUp 4.5s ease-in forwards;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.08);
}

body.dark .floating-reaction {
  background: rgba(148, 163, 184, 0.25);
}

@keyframes floatUp {
  0% {
    transform: translateY(0) scale(0.95);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  60% {
    transform: translateY(-240px) scale(1.02);
    opacity: 0.9;
  }
  100% {
    transform: translateY(-420px) scale(1);
    opacity: 0;
  }
}
